<template>
<div>
<div class="page" id="home">
   
    <!-- 头部 -->
    <home-header></home-header>

    <!-- 头部导航栏 -->
    <home-menu v-model="selectNavIndex"></home-menu>

    <!-- 推荐结构 -->
    <app-scroll class="content" v-if="selectNavIndex === 0">
        <!-- 轮播图 -->
        <app-swiper class="banner" ref="swiper">
            <app-swiper-item v-for="item in bannerData" :key="item.id">
                <img :src="item.picUrl"/>
            </app-swiper-item>
        </app-swiper>
       
        <!-- policy 三个用户享受权益 -->
        <home-policy ref="kinds"></home-policy>

        <!--  新品首发list-->
        <home-kinds></home-kinds>

        <!-- 新人专享礼 -->
        <home-fresher></home-fresher>

          <!-- 品牌制造商直销 -->
        <home-brand></home-brand>

        <!-- 类目热销榜 -->
        <home-hot></home-hot>
         
        <!-- 人气推荐榜 -->
        <home-popular></home-popular>

        <!-- 限时购 -->
        <home-limitedBuy></home-limitedBuy>

         <!-- 新品首发 -->
        <home-newItemList></home-newItemList>

        <!-- 购买指南 -->
        <home-shopGuide></home-shopGuide>

        <!-- 底部轮播图 -->
        <home-cateBanner></home-cateBanner>


    </app-scroll>
</div>

<transition enter-active-class="slideInRight" leave-active-class="slideOutRight">
    <router-view></router-view>
</transition>

</div>
</template>


<script>
import header from './children/header'
import menu from './children/menu'
import policy from './children/policy'
import kindslist from './children/kindsList'
import fresher from './children/fresher'
import brand from './children/brand'
import hot from './children/hot'
import popular from './children/popular'
import limitedBuy from './children/limitedBuy'
import newItemList from './children/newItemList'
import shopGuide from './children/shopGuide'
import cateBanner from './children/cateBanner'

import {mapState} from 'vuex'
export default {
    components: {
        [header.name]: header,
        [menu.name]: menu,
        [policy.name]: policy,
        [kindslist.name]: kindslist,
        [fresher.name]: fresher,
        [brand.name]: brand,
        [hot.name]: hot,
        [popular.name]: popular,
        [limitedBuy.name]: limitedBuy,
        [newItemList.name]: newItemList,
        [shopGuide.name]: shopGuide,
        [cateBanner.name]: cateBanner,
    },
    data(){
        return {
            selectNavIndex: 0
        }
    },
    computed: {
        ...mapState({
            bannerData: state=>state.home.bannerData,
        }),
    },
    watch: {
        selectNavIndex(){
            console.log('下标变化了,切换页面');
        }
    },
    methods: {
        //请求首页的初始化数据
        getHomeInitData(){
            //导航
            this.$store.dispatch('home/requestHomeNavData');
            
            //轮播图
            this.$store.dispatch('home/requestHomeBannerData');
           
            //新品首发
            this.$store.dispatch('home/requestHomeKindsData');

            //新人专享礼
            this.$store.dispatch('home/requestHomeFresherData');

            //品牌制造商直销
            this.$store.dispatch('home/requestHomeBrandData');

            //类目热销榜
            this.$store.dispatch('home/requestHomeHotData');

            //人气推荐榜
            this.$store.dispatch('home/requestHomePopularData');

            //限时购
            this.$store.dispatch('home/requestHomeLimitedBuyData');

            //新品首发
            this.$store.dispatch('home/requestHomeNewItemListData');

            //购物指南
            this.$store.dispatch('home/requestHomeShopGuideData');
            
            //底部轮播图
            this.$store.dispatch('home/requestHomeCateBannerData');
        },
    },
    created(){
        //获得页面数据
        this.getHomeInitData();

    },
    updated(){
        //更新轮播图
        this.$refs.swiper.update();
        // this.$refs.kinds.update();
    }
}
</script>


