<template>
<div class="nav">
    <div class="list-wrap" ref="menuScroll">
        <ul class="menu-list">
            <router-link to="home/cate/11" class="menu-item" :class="{active: value === index}" 
                v-for="(item, index) in navData" :key="item.id"
                @click="selectAction(index)">
                {{item.name}}
            </router-link >
        </ul>
    </div>

    <transition enter-active-class="slideInDown" leave-active-class="slideOutUp">
        <div class="table-wrap" v-show="isShow">
            <div class="cover"></div>
            <div class="menu">
                <div>
                    <span class="title">全部频道</span>
                    <div class="arrow" :class="{active: isShow}" @click="changeAction">
                        <img src="../../../../assets/arrow.png"/>
                    </div>
                </div>

                <div class="menu-table">

                <div class="menu-item" :class="{active: value === index}" 
                    v-for="(item, index) in navData" :key="item.id"
                    @click="selectAction(index)">
                    {{item.name}}
                </div>
            </div>
            </div>
        </div>
    </transition>

    <div class="arrow" :class="{active: isShow}" @click="changeAction">
        <img src="../../../../assets/arrow.png"/>
    </div>

</div>
</template>

<script>
import { mapState } from "vuex";
import BScroll from "better-scroll";
export default {
  name: "home-menu",
  props: {
    value: Number
  },
  data() {
    return {
      isShow: false
    };
  },
  computed: {
    ...mapState({
      navData: state => state.home.navData
    })
  },
  methods: {
    changeAction() {
      this.isShow = !this.isShow;
      // console.log(this.isShow)
    },
    selectAction(index) {
      this.$emit("input", index);
      this.isShow = false;
    }
  },
  mounted() {
    new BScroll(this.$refs.menuScroll, {
      click: true,
      tap: true,
      scrollX: true,
      scrollY: false
    });
  }
};
</script>

